

export interface FollowerRange {
	label?: string;
	lower: number;
	upper?: number;
}

export interface DeliverableWithCount {
	deliverable: string;
	count: number;
}

interface Money {
	amount: number;
	currency: string;
}

interface CPADetails {
	productLink?: string;
	rate?: number;
	priceAfterDiscount?: number;
}

interface BarterPayout {
	type: PayoutType.BARTER;
	productMRP: Money;
	productUrl: string;
}

interface FixedPayout {
	type: PayoutType.FIXED;
	cash: Money;
}

interface VariablePayout {
	type: PayoutType.VARIABLE;
	maxCash: Money;
}
interface CPA {
	type: PayoutType.CPA;
	details: CPADetails;
}
export type Payout = BarterPayout | FixedPayout | VariablePayout | CPA;

