export interface PayoutElement {
	readonly payoutType?: PayoutType | keyof typeof PayoutType;
	readonly maxMilestones?: number;
}

export interface FolloweRange {
	readonly label: string;
	readonly minFollowers: number;
	readonly maxFollowers: number;
}

export interface DeliveryMilestone {
	 milestoneNo: number;
	 milestoneTitle: string;
	 dueDate: string;
	 percentage: number;
	 status?: string;
}

export interface Deliverables {
	readonly deliverableType?: DeliverableType;
	readonly count?: number;
}

export interface DeliverableType {
	readonly deliverableYoutube?:
		| DeliverableTypeYoutube
		| keyof typeof DeliverableTypeYoutube;
	readonly deliverableInsta?:
		| DeliverableTypeInsta
		| keyof typeof DeliverableTypeInsta;
}

export interface PushNotificationDeviceToken {
	readonly service?:
		| PushNotificationService
		| keyof typeof PushNotificationService;
	readonly deviceTokens?: (string | null)[];
}

export interface CampaignSubmission {
	readonly influencerId: string;
	readonly milestones?: (DeliveryMilestone | null)[];
	readonly quizAnswer?: (QuizElement | null)[];
	readonly status?: JobStatus | keyof typeof JobStatus;
}

export interface QuizElement {
	question?: string;
	options?: (string | null)[];
}
export interface QuizAnswer {
	question?: string;
	answer?: string;
}



export interface Application {
	actionOn?: string;
	selectedIdx?: number | undefined;
	_id?: string;
	applicationId?: string;
    campaignId?: string; 
    applicationStatus?: string;
    influencerId?: string;
    influencerName?: string;
    socialaccount?:string;
    followers?: number;
    engagement?: number;
    gender?: string;
    age?: string;
    location?: string;
    userId?: string;
	categories:  (Category | null)[];
    deliverables?:(Deliverables | null)[];
    isCompleted?: boolean;
    acceptedAt?: Date;
    completedAt?: Date;
    dueDate?: Date;
    mobileNo?: string;
    email?: string;
    language?: string;
    quizAnswers?: (QuizAnswer | null)[];
    milestonesCustomized?: boolean;
    milestones?: (DeliveryMilestone | null)[];
    payouts?:(Payout | null)[];
}


